import { Button, Modal, Table } from 'antd';
import React, { useEffect } from 'react'

type ModalProps = {
    open: boolean;
    setOpen: React.Dispatch<React.SetStateAction<boolean>>;
    totalTerm: number;
    loan: number;
    interestRate: number;
}

