const columns = [
    {
        title: 'Kì',
        dataIndex: 'periodically',
        key: 'periodically',
    },
    {
        title: 'Số tiền',
        dataIndex: 'AmountOfMoney',
        key: 'AmountOfMoney',
    },
    {
        title: 'Ngày đóng',
        dataIndex: 'ClosingDate',
        key: 'ClosingDate',
    },
];


export default function ModalTable({ open, setOpen, totalTerm, loan, interestRate }: ModalProps) {

    const [dataSource, setDataSource] = React.useState<any[]>([])

    const handleShowDetail = (totalTerm: number, loan: number, interestRate: number) => {

        const principalPerMonth = loan / totalTerm; // tiền gốcMỗi tháng
        const FirstMonthInterestRate = (loan * interestRate) / 100; // tiền lãi tháng đầu

        const data = [];

        for (let i = 1; i <= totalTerm; i++) {
            const date = new Date();
            date.setMonth(date.getMonth() + i);

            let total = 0;
            if (i === 1) {
                total = principalPerMonth + FirstMonthInterestRate;
            } else {
                total = principalPerMonth + ((loan - (i - 1) * principalPerMonth) * interestRate) / 100;
            }

            data.push({
                key: i,
                periodically: `Kì thứ ${i}`,
                AmountOfMoney: formatMoney(total),
                ClosingDate: formatDate(date),
            })
        }

        setDataSource(data)



    }

    // Format tiền
    function formatMoney(number: number) {
        number = Math.round(number * 1000) / 1000;

        return number
            .toFixed(0)
            .replace(/\D/g, "")
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    // Format ngày
    function formatDate(date: Date) {
        return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
    }

    useEffect(() => {
        handleShowDetail(totalTerm, loan, interestRate)
    }, [open])

    console.log(totalTerm, loan, interestRate);


    return (
        <Modal
            open={open}
            onCancel={() => setOpen(false)}
            footer={[
                <Button key="close" onClick={() => setOpen(false)}>
                    Đóng
                </Button>
            ]}
        >
            <Table dataSource={dataSource} columns={columns} />
        </Modal>
    )
}
