import { Empty, Modal } from "antd"
import { collection, getDocs, query, where } from "firebase/firestore"
import { useEffect, useState } from "react"
import Button from "../../../components/Button"
import { db } from "../../../configs/firebase.config"
import { useAppSelector } from "../../../hooks/useRedux"
import ModalTable from "../../../components/Modal"
import Contract from "../Debit/ConfirmDebit/Contract"

export default function ContractPage() {

    const uid = useAppSelector(state => state.auth.uid)
    const [listLoan, setListLoan] = useState<any[]>([])

    const [isOpenModalDetail, setIsOpenModalDetail] = useState<boolean>(false)
    const [isShowDetail, setIsShowDetail] = useState<any | null>(null)

    const [isShowContract, setIsShowContract] = useState<boolean>(false)
    const [isShowContractDetail, setIsShowContractDetail] = useState<any | null>(null)

    const getLoanList = async () => {
        const collectionRef = collection(db, "loan-list");
        const q = query(collectionRef, where("uid", "==", uid));
        const querySnapshot = await getDocs(q);
        const data: any[] = [];

        querySnapshot.forEach(doc => {
            data.push({ ...doc.data(), id: doc.id })
        });

        setListLoan(data);
    }

    // Format tiền
    function formatMoney(number: number) {
        number = Math.round(number * 1000) / 1000;

        return number
            .toFixed(0)
            .replace(/\D/g, "")
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }


    useEffect(() => {
        getLoanList()
    }, [])



    return (
        <div className="p-4 flex flex-col items-center gap-4">
            <h1 className="text-primary font-bold text-lg">Thông tin hợp đồng của bạn</h1>
            <div className="flex flex-col gap-8 w-full">
                {
                    listLoan ? listLoan.length > 0 ? listLoan.map((loan: any, index: number) => (
                        <div key={index} className=" w-full flex flex-col gap-2 items-start">
                            <div className="flex w-full justify-between">
                                <p className="flex-1">Loại giao dịch :</p>
                                <p className="font-bold flex-1">Hợp đồng vay tiền</p>
                            </div>
                            <div className="flex w-full justify-between">
                                <p className="flex-1">Mã hợp đồng :</p>
                                <p className="flex-1">{loan.idContract}</p>
                            </div>
                            <div className="flex w-full justify-between">
                                <p className="flex-1">Số tiền vay :</p>
                                <p className="font-bold flex-1">{formatMoney(loan.loan.loanAmount)}</p>
                            </div>
                            <div className="flex w-full justify-between">
                                <p className="flex-1">Hạn thanh toán :</p>
                                <p className="flex-1">{loan.loan.loanMonth} tháng</p>
                            </div>
                            <div className="flex w-full justify-between">
                                <p className="flex-1"> Khởi tạo lúc :</p>
                                <p className="flex-1">{loan.BorrowedTime}</p>
                            </div>
                            <div className="flex w-full justify-between">
                                <p className="flex-1">
                                    Tình trạng :
                                </p>
                                <p className="flex-1">{
                                    loan.status === 'pending' ? 'Đang chờ duyệt' : loan.status === 'approved' ? 'Đã duyệt' : 'Đã hủy'
                                }</p>
                            </div>
                            <Button className="font-bold text-primary text-sm"
                                onClick={() => {
                                    setIsOpenModalDetail(true)
                                    setIsShowDetail(loan.loan)
                                }}
                            >Chi tiết trả nợ</Button>
                            <div className="w-full flex justify-center">
                                <Button className="py-1 px-4 rounded-full bg-orange-500 font-semibold text-white"
                                    onClick={() => {
                                        setIsShowContract(true)
                                        setIsShowContractDetail(loan)
                                    }}
                                >
                                    Xem hợp đồng
                                </Button>
                            </div>
                        </div>
                    )) : (<div>
                        <Empty description="Bạn chưa có hợp đồng nào" />
                    </div>) : (
                        <Empty />
                    )
                }
            </div>
            <Modal
                open={isShowContract}
                onOk={() => setIsShowContract(false)}
                closeIcon={null}
                footer={[
                    <div key="print" className=" flex justify-end items-center gap-2 py-2 border-t  border-gray-200">
                        <Button className="py-2 px-4 rounded-lg bg-[#29badb] text-white text-sm" onClick={() => {
                            console.log('print');
                        }}>
                            In PDF
                        </Button>
                        <Button key="close" className="py-2 px-4 rounded-lg bg-[#3d78e3] text-white text-sm" onClick={() => setIsShowContract(false)}>
                            Đóng
                        </Button>
                    </div>
                ]}
            >
                <Contract IdContract={isShowContractDetail?.idContract} Borrower={isShowContractDetail?.fullName} Amount={isShowContractDetail?.loan?.loanAmount} CCCD={isShowContractDetail?.cmnd} Date={isShowContractDetail?.BorrowedTime} InterestRate={isShowContractDetail?.loan?.loanInterestRate} Time={isShowContractDetail?.loan?.loanMonth} />
            </Modal>
            {
                isOpenModalDetail && <ModalTable open={isOpenModalDetail} setOpen={setIsOpenModalDetail} interestRate={isShowDetail.loanInterestRate} loan={isShowDetail.loanAmount} totalTerm={isShowDetail.loanMonth} />
            }
        </div>
    )
}
